*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

html {
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #3c425f;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  min-width: 1440px;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}

.page {
  display: grid;
  grid-template-areas: "navigation header" "navigation main" "footer footer";
  grid-template-columns: 280px 1fr;
  grid-template-rows: 80px 1fr 60px;
  min-height: 100vh;
}
.page-menu {
  grid-area: navigation;
  display: grid;
  grid-template-rows: 80px 1fr auto;
  background-color: #ffffff;
  border-right: 1px solid #b4bef0;
}
.page-menu-logo {
  margin: 24px 30px 15px;
}
.page-menu-list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.page-menu-item {
  position: relative;
}
.page-menu-item-active {
  background-color: #f1f3fe;
}
.page-menu-item-active::before {
  content: "";
  position: absolute;
  width: 6px;
  height: 60px;
  left: 0;
  top: 0;
  background-image: url("../img/active-menu-item.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.page-menu-item-active .page-menu-link {
  font-weight: 500;
  color: #576bcc;
}
.page-menu-link {
  position: relative;
  display: block;
  padding: 20px 25px 20px 63px;
  font-size: 16px;
  line-height: 20px;
  color: #3c425f;
}
.page-menu-link::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  left: 30px;
}
.page-menu-link-cab::before {
  background-image: url("../img/cabinet.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.page-menu-link-class::before {
  background-image: url("../img/class.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.page-menu-link-book::before {
  background-image: url("../img/book.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.page-menu-link-home::before {
  background-image: url("../img/home.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.page-menu-link-exam::before {
  background-image: url("../img/exam.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.page-menu-tools {
  padding: 0;
  margin: 0 0 10px 0;
  list-style: none;
}
.page-menu-tools-link {
  position: relative;
  display: block;
  padding: 20px 25px 20px 63px;
  font-size: 16px;
  line-height: 20px;
  color: #3c425f;
}
.page-menu-tools-link::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  left: 30px;
  background-image: url("../img/settings.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.page-header {
  grid-area: header;
  display: grid;
  grid-template-columns: 1fr auto 80px;
  background-color: #ffffff;
  border-bottom: 1px solid #b4bef0;
}
.page-header-search {
  margin-top: 10px;
  margin-left: 30px;
}
.page-header-profile {
  margin: 20px 20px 20px 0;
}
.page-header-notifications {
  margin: 27px;
}
.page-main {
  grid-area: main;
  padding: 30px;
  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-template-rows: 300px auto 1fr;
  grid-template-areas: "slider contacts" "offers contacts" "video contacts";
  grid-gap: 20px;
  background-color: #f1f3fe;
}
.page-footer {
  grid-area: footer;
  display: grid;
  grid-template-columns: 280px 1fr 280px;
  background-color: #3c425f;
}
.page-footer-copyright {
  margin: 20px 10px 10px 30px;
}
.page-footer-logo {
  margin: 20px 0;
  text-align: center;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  color: #aaaaaa;
}

.search-field {
  padding: 20px 20px 20px 32px;
  font-family: inherit;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  color: #3c425f;
  border: none;
  background-image: url("../img/search.svg");
  background-repeat: no-repeat;
  background-size: 20px 20px;
  background-position: left center;
}
.search-field::placeholder {
  color: #b4bef0;
}

.profile {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 10px;
  max-width: 400px;
}
.profile-with-picture::before {
  content: attr(data-text);
  width: 40px;
  height: 40px;
  font-weight: 900;
  font-size: 22px;
  line-height: 40px;
  text-align: center;
  color: #b4bef0;
  border-radius: 50%;
  background-color: #f1f3fe;
}
.profile-name {
  font-size: 16px;
  line-height: 40px;
  color: #3c425f;
}
.profile-btn {
  position: relative;
  width: 30px;
  border: none;
  background-color: transparent;
}
.profile-btn::before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border: 6px solid #576bcc;
  border-left-color: transparent;
  border-right-color: transparent;
  border-bottom-color: transparent;
}

.notifications-btn {
  width: 25px;
  height: 25px;
  background-image: url("../img/notifications.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px 20px;
  background-color: transparent;
  border: none;
}
.notifications-btn span {
  display: inline-block;
  position: relative;
  right: -5px;
  top: -5px;
  width: 18px;
  height: 18px;
  font-weight: 900;
  font-size: 10px;
  line-height: 14px;
  color: #ffffff;
  background-color: #ff8f0b;
  border-radius: 50%;
  border: 2px solid #ffffff;
}

.slider {
  position: relative;
  grid-area: slider;
  width: 820px;
  height: 300px;
  padding: 32px 60px;
  overflow: hidden;
  box-sizing: border-box;
  font-weight: 300;
  font-size: 18px;
  line-height: 28px;
}
.slider-img {
  position: absolute;
  left: 0;
  top: 0;
}
.slider-headline {
  position: relative;
  margin-bottom: 10px;
  font-weight: 900;
  font-size: 28px;
  line-height: 48px;
  color: #ffffff;
}
.slider-text {
  position: relative;
  margin: 0 0 30px 0;
  color: #ffffff;
  max-width: 470px;
}
.slider-link {
  position: relative;
  padding: 10px 20px;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  background-color: #ff8f0b;
  border-radius: 4px;
}
.slider-btn {
  position: absolute;
  width: 64px;
  height: 64px;
  margin: auto;
  border-radius: 50%;
  border: none;
  background-color: #ffffff;
  background-image: url("../img/arrow.svg");
  background-repeat: no-repeat;
  background-size: 18px 18px;
  background-position: left 35px center;
}
.slider-btn-prev {
  left: -32px;
  top: 0;
  bottom: 0;
}
.slider-btn-next {
  left: auto;
  right: -32px;
  top: 0;
  bottom: 0;
  transform: rotate(180deg);
}

.offers {
  grid-area: offers;
}
.offers-list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
}
.offers-item {
  display: grid;
  grid-template-rows: auto 1fr auto;
  padding: 20px 30px;
  background-color: #ffffff;
  box-shadow: 0 1px 4px rgba(60, 66, 95, 0.2);
  border-radius: 4px;
}
.offers-title {
  margin: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #3c425f;
}
.offers-item-text {
  margin: 5px 0 5px 0;
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  color: #3c425f;
}
.offers-item-time {
  margin: 0 0 8px 0;
  font-weight: 900;
  font-size: 14px;
  line-height: 20px;
  color: #0bc4cf;
}
.offers-footer {
  text-align: right;
}
.offers-link {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #576bcc;
}

.video {
  grid-area: video;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: 20px;
}
.video-lesson {
  width: 540px;
  height: 300px;
  border-radius: 4px;
}
.video-title {
  margin: 20px 0 13px 30px;
  padding-bottom: 10px;
  border-bottom: 1px solid #e4e8fc;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
}
.video-play-list-wrapper {
  position: relative;
  height: 300px;
  background: #ffffff;
  box-shadow: 0 1px 4px rgba(60, 66, 95, 0.2);
  border-radius: 4px;
}

.contacts {
  grid-area: contacts;
  padding: 20px 0 20px 30px;
  background-color: #ffffff;
  box-shadow: 0 1px 4px rgba(60, 66, 95, 0.2);
  border-radius: 4px;
}
.contacts-title {
  margin: 0 0 10px 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #3c425f;
}
.contacts-list {
  padding: 5px 0 0 0;
  margin: 0;
  list-style: none;
  border-top: 1px solid #e4e8fc;
}
.contacts-item {
  padding: 16px 10px 0 0;
}
.contacts-link {
  display: grid;
  grid-template-columns: 40px auto;
  grid-template-rows: auto auto;
  grid-column-gap: 12px;
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  color: #3c425f;
}
.contacts-link::before {
  grid-row: 1/3;
}
.contacts-name {
  grid-column: 2/3;
  grid-row: 1/2;
  margin: 0;
}
.contacts-lang {
  grid-column: 2/3;
  grid-row: 2/3;
  margin: 0;
}

.play-list {
  padding: 0;
  margin: 0;
  list-style: none;
  height: 235px;
  overflow-y: auto;
}
.play-item {
  display: grid;
  grid-template-columns: 32px auto;
  grid-template-rows: auto auto;
  grid-template-areas: "button title" "button desc";
  grid-column-gap: 13px;
  padding: 8px 10px 8px 30px;
  font-weight: 300;
  font-size: 14px;
  line-height: 16px;
  color: #3c425f;
}
.play-item-active {
  background-color: #f1f3fe;
}
.play-item-active .play-button {
  background-color: #576bcc;
}
.play-item-active .play-button::before {
  border-left-color: #ffffff;
}
.play-button {
  grid-area: button;
  position: relative;
  width: 32px;
  height: 32px;
  background-color: #f1f3fe;
  border-radius: 4px;
  border: none;
}
.play-button::before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  left: 0;
  top: 0;
  transform: translate(12px, 10px);
  border-top: 6px solid rgba(255, 255, 255, 0);
  border-left: 12px solid #576bcc;
  border-bottom: 6px solid rgba(255, 255, 255, 0);
  border-right: 12px solid rgba(255, 255, 255, 0);
}
.play-title {
  grid-area: title;
  margin: 0;
  font-weight: 900;
}
.play-desc {
  grid-area: desc;
  margin: 0;
}

.copyright {
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  color: #aaaaaa;
}
.copyright-details {
  margin: 20px 30px 10px 10px;
  text-align: right;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  color: #aaaaaa;
}
.copyright-link {
  color: #aaaaaa;
}

